.profile {
  padding-top: 40px;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 767px){
  .profile {
    padding-top: 42px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}


